<script setup>
import { ref } from "vue";
import axios from "axios";

import { LockOpenIcon } from "@heroicons/vue/24/outline";

// Definimos los valores iniciales
const key = ref("");
const name = ref("");
const email = ref("");
const phone = ref("");
const address = ref("");
const creditCard = ref("");
const password = ref("");

const encryptedData = ref(null);
const decryptedData = ref(null);
const keyDecrypt = ref(""); // Clave de descifrado

const encryptData = async () => {
  try {
    const response = await axios.post(
      "https://recipehub-api-main.vercel.app/encrypt",
      {
        key: key.value,
        name: name.value,
        email: email.value,
        phone: phone.value,
        address: address.value,
        credit_card: creditCard.value,
        password: password.value,
      }
    );

    encryptedData.value = response.data;
  } catch (error) {
    console.error("Error al cifrar los datos:", error);
  }
};

const decryptData = async () => {
  try {
    const response = await axios.post(
      "https://recipehub-api-main.vercel.app/decrypt",
      {
        key: key.value, // Enviar la clave usada en el cifrado original
        encrypted_name: encryptedData.value.encrypted_name,
        encrypted_email: encryptedData.value.encrypted_email,
        encrypted_phone: encryptedData.value.encrypted_phone,
        encrypted_address: encryptedData.value.encrypted_address,
        encrypted_credit_card: encryptedData.value.encrypted_credit_card,
      }
    );
    decryptedData.value = response.data;
    console.log(decryptedData.value);
  } catch (error) {
    console.error("Error al descifrar los datos:", error);
  }
};
</script>

<template>
  <section
    class="container-django"
    :class="{ 'container-django--two-columns': encryptedData }"
  >
    <form class="form form-encrypt" @submit.prevent="encryptData">
      <h1 class="form-title text-center mb-bg">Cifrado de datos en Express</h1>
      <div class="form-group">
        <div class="form-group form-label--help">
          <label for="key">Clave para descifrar</label>
          <span>?</span>
        </div>
        <input
          type="text"
          class="form-control"
          placeholder="MySecretPassword123@@"
          v-model="key"
          id="key"
        />
      </div>
      <div class="form-group">
        <label for="name">Nombre</label>
        <input
          type="text"
          class="form-control"
          placeholder="Jane Doe"
          v-model="name"
          id="name"
        />
      </div>
      <div class="form-group">
        <label for="email">Correo Electrónico</label>
        <input
          type="email"
          class="form-control"
          placeholder="jane_doe@outlook.com"
          v-model="email"
          id="email"
        />
      </div>
      <div class="form-group form-group--two-columns">
        <div class="form-group">
          <label for="phone_number">Télefono</label>
          <input
            type="phone"
            class="form-control"
            placeholder="239 555 0108"
            v-model="phone"
            id="phone_number"
          />
        </div>
        <div class="form-group">
          <label for="address">Dirección</label>
          <input
            type="text"
            class="form-control"
            placeholder="234 Fake Street"
            v-model="address"
            id="address"
          />
        </div>
      </div>
      <div class="form-group">
        <label for="credit_card">Tarjeta de crédito</label>
        <input
          type="text"
          class="form-control"
          placeholder="4024 0071 7677 8545"
          v-model="creditCard"
          id="credit_card"
        />
      </div>
      <div class="form-group">
        <label for="password">Contraseña</label>
        <input
          type="password"
          class="form-control"
          placeholder="***************"
          v-model="password"
          id="password"
        />
      </div>
      <button class="btn btn-md btn-primary mt-bg">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke-width="2"
          stroke="currentColor"
          class="icon"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            d="M16.5 10.5V6.75a4.5 4.5 0 1 0-9 0v3.75m-.75 11.25h10.5a2.25 2.25 0 0 0 2.25-2.25v-6.75a2.25 2.25 0 0 0-2.25-2.25H6.75a2.25 2.25 0 0 0-2.25 2.25v6.75a2.25 2.25 0 0 0 2.25 2.25Z"
          />
        </svg>
        Cifrar datos
      </button>
      <!-- <input class="btn btn-md btn-primary mt-bg" type="submit" value="Cifrar datos"> -->
    </form>

    <div class="container-decrypt" v-if="encryptedData">
      <article class="form information-container">
        <h2 class="information-title text-center title-sticky">
          Datos cifrados
        </h2>
        <hr />
        <div class="form-group">
          <h3>Nombre: <span class="text-primary">(RC6)</span></h3>
          <p>{{ encryptedData.encrypted_name || "" }}</p>
        </div>
        <hr />
        <div class="form-group">
          <h3>Correo electrónico: <span class="text-primary">(RC6)</span></h3>
          <p>{{ encryptedData.encrypted_email || "" }}</p>
        </div>
        <hr />
        <div class="form-group">
          <h3>Teléfono: <span class="text-primary">(RABIN)</span></h3>
          <p>{{ encryptedData.encrypted_phone || "" }}</p>
        </div>
        <hr />
        <div class="form-group">
          <h3>Dirección: <span class="text-primary">(RC6)</span></h3>
          <p>{{ encryptedData.encrypted_address || "" }}</p>
        </div>
        <hr />
        <div class="form-group">
          <h3>Tarjeta de crédito: <span class="text-primary">(RABIN)</span></h3>
          <p>{{ encryptedData.encrypted_credit_card || "" }}</p>
        </div>
        <hr />
        <div class="form-group">
          <h3>Contraseña: <span class="text-primary">(HASH)</span></h3>
          <p>{{ encryptedData.encrypted_password || "" }}</p>
        </div>
      </article>

      <form @submit.prevent="decryptData" class="form form-decrypt">
        <div class="form-group">
          <h2 class="text-center">Clave secreta para descifrar</h2>
          <input
            type="text"
            class="form-control"
            placeholder="MySecretPassword123@"
            v-model="keyDecrypt"
          />
        </div>
        <button class="btn btn-md btn-primary">
          <LockOpenIcon class="icon" />

          Descifrar datos
        </button>
      </form>

      <article
        class="form information-container information-container--decrypt"
        v-if="decryptedData"
      >
        <h2 class="information-title text-center title-sticky">
          Datos descifrados
        </h2>
        <div class="form-group">
          <h4>Nombre:</h4>
          <p>{{ decryptedData.decrypted_name }}</p>
        </div>
        <div class="form-group">
          <h4>Correo electrónico:</h4>
          <p>{{ decryptedData.decrypted_email }}</p>
        </div>
        <div class="form-group">
          <h4>Teléfono:</h4>
          <p>{{ decryptedData.decrypted_phone }}</p>
        </div>
        <div class="form-group">
          <h4>Dirección:</h4>
          <p>{{ decryptedData.decrypted_address }}</p>
        </div>
        <div class="form-group">
          <h4>Tarjeta de crédito:</h4>
          <p>{{ decryptedData.decrypted_credit_card }}</p>
        </div>
        <div class="form-group">
          <h4>Contraseña:</h4>
          <p>No es posible descifrar una contraseña en HASH</p>
        </div>
      </article>
    </div>
  </section>
</template>

<style scoped>
.container-django {
  min-height: 100vh;
  padding: 2.4rem;

  display: grid;
  place-content: center;
  gap: 3.2rem;
  align-items: flex-start;
}

.container-decrypt {
  display: grid;
  gap: 3.2rem;
}

@media (min-width: 768px) and (max-width: 1024px) {
  .container-django {
    padding: 3.2rem;
    gap: 1.6rem;
  }

  .container-django--two-columns {
    grid-template-columns: repeat(2, 1fr);
  }

  .container-decrypt {
    gap: 1.6rem;
    place-content: start;
  }
}

@media (min-width: 1025px) {
  .container-django {
    padding: 16rem;
    gap: 3.2rem;
  }

  .container-decrypt {
    gap: 3.2rem;
    place-content: start;
  }

  .container-django--two-columns {
    grid-template-columns: 40rem 50rem;
  }
}

.information-container--decrypt {
  gap: 8px;
}
</style>
